:root {
    --gray-bg: #424242;
    --border-color:#6e6e6e;
}
html, body {
    overflow: hidden;
}
img {
    user-select: none;
    -moz-user-select: none;
    -webkit-user-drag: none;
    -webkit-user-select: none;
    -ms-user-select: none;
}
.react-swipeable-view-container{
    height:100%;
}
.rst__rowContents {
    background: #424242!important;
    min-width:auto;
}
.rst__moveHandle{
    background-color:#f57c00!important;
}
.playlist-selected{
    border-right: 2px solid #f57c00;
    border-radius:1px;
    color:#f57c00;
}
.rs-picker-default .rs-btn, .rs-picker-input .rs-btn, .rs-picker-default .rs-picker-toggle, .rs-picker-input .rs-picker-toggle{
    background: var(--gray-bg) !important;
}
.rs-picker-menu{
    background: var(--gray-bg) !important;
}
.rs-picker-menu .rs-picker-search-bar .rs-picker-search-bar-input{
    background:#303030;
}
 a:hover, a:focus {
    color: inherit;
    text-decoration: none;
 }
.rs-tree{
    background: var(--gray-bg);
    height: 40vh;
}
.rs-input{
    background: transparent;
    border-color: var(--border-color);
    text-align: center;
    padding:4px !important;
}
.rs-input-number{
    background: transparent;
    border-color: var(--border-color) !important;
}
.rs-btn-subtle{
    background: transparent;
    color:white;
}
.rs-btn-subtle:hover{
    background: unset;
    color:white;
}
.rs-input-group-addon.rs-input-group-addon-disabled, .rs-input-group-addon{
    background: transparent;
    padding:0 !important;
    min-width: unset !important;
}
.rs-btn-subtle:focus{
    background: transparent;
    color:unset;
}
.rs-input[disabled]{
    background:transparent;
}
.rs-input-group{
    border-color:var(--border-color) !important;
}
.rs-input-group-disabled{
    background:transparent;
}
.rs-input-group-btn{
    background:transparent !important;
}
.tree-picker-license > a{
    border-color: var(--border-color) !important;
}
.rs-picker-menu{
    z-index: 100000;
}
.rs-picker-default .rs-picker-toggle{
    border-color: var(--border-color);
}
.rs-tree-node-label{
    width:100%;
}
.rs-picker-toggle-wrapper  {
    width:100%;
}
